import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PortfolioComponent } from './portfolio/portfolio.component';

const routes: Routes = [
  // default redirection
  { path: '', redirectTo: 'Programming/Web Development', pathMatch: 'full' },
  { path: 'Programming', redirectTo: 'Programming/Web Development', pathMatch: 'full' },
  { path: 'Design', redirectTo: 'Design/Social Media Designs', pathMatch: 'full' },
  { path: 'Video', redirectTo: 'Video/Motion Graphic', pathMatch: 'full' },
  { path: 'Audio', redirectTo: 'Audio/Audio Engineering', pathMatch: 'full' },
  { path: 'Others', redirectTo: 'Others/Photography', pathMatch: 'full' },
  {
    path: 'Programming', component: PortfolioComponent, children: [
      { path: 'Web Development', component: PortfolioComponent },
      { path: 'Ux Ui', component: PortfolioComponent },
      { path: 'Desktop', component: PortfolioComponent },
      { path: 'Game', component: PortfolioComponent },
    ]
  },
  {
    path: 'Design', component: PortfolioComponent, children: [
      { path: 'Social Media Designs', component: PortfolioComponent },
      { path: 'Brand Design', component: PortfolioComponent },
      { path: '3d Design', component: PortfolioComponent },
      { path: 'Art & Drawing', component: PortfolioComponent }
    ]
  },
  {
    path: 'Video', component: PortfolioComponent, children: [
      { path: 'Motion Graphic', component: PortfolioComponent },
      { path: 'Video Editing', component: PortfolioComponent }
    ]
  },
  {
    path: 'Audio', component: PortfolioComponent, children: [
      { path: 'Audio Engineering', component: PortfolioComponent },
      { path: 'Music', component: PortfolioComponent },
      { path: 'Voice Acting', component: PortfolioComponent }
    ]
  },
  {
    path: 'Others', component: PortfolioComponent, children: [
      { path: 'Photography', component: PortfolioComponent },
      { path: 'Behind The Scenes', component: PortfolioComponent },
      { path: 'Me', component: PortfolioComponent }
    ]
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    initialNavigation: 'enabledBlocking'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
